/* ✅ PROJECTS SECTION */
.projects-section {
  background-color: #0a192f;
  padding: 60px 5%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 1s ease-in-out, transform 1s ease-in-out;
}

/* ✅ Show Projects When in View */
.projects-section.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Section Title */
.section-title {
  color: white;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 30px;
  text-align: center;
}

/* ✅ Mobile View: Stacked Project Cards */
.project-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
}

/* ✅ PROJECT CARD */
.project-card {
  background-color: #112240;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: calc(100% - 20px);
  text-align: left;
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: relative;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 1s ease-out forwards 0.4s;
}

/* ✅ Folder Icon */
.folder-icon {
  font-size: 30px;
  color: #64ffda;
  position: absolute;
  top: 15px;
  left: 15px;
}

/* Project Title */
.project-title {
  color: #64ffda;
  font-size: 18px;
  font-weight: bold;
  margin-top: 40px;
}

/* Project Description */
.project-description {
  color: #8892b0;
  font-size: 14px;
  line-height: 1.4;
}

/* Technology Stack */
.tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.tech-item {
  color: #8892b0;
  font-size: 12px;
  background: rgba(255, 255, 255, 0.1);
  padding: 5px 10px;
  border-radius: 5px;
  text-transform: uppercase;
}

/* ✅ Desktop View: Carousel */
.carousel {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 500px;
}

/* PROJECT SLIDE */
.project-slide {
  background-color: #112240;
  padding: 20px;
  border-radius: 8px;
  transition: 0.3s ease-in-out;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 100%;
  min-height: 250px;
  text-align: center;
  position: relative;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 1s ease-out forwards 0.6s;
}

/* ✅ Folder Icon in Web View (Top Left) */
.project-slide .folder-icon {
  top: 15px;
  left: 15px;
  position: absolute;
}

/* LEFT & RIGHT ARROWS */
.arrow {
  background: transparent;
  border: none;
  color: #64ffda;
  font-size: 24px;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: 0.3s ease-in-out;
}

.arrow.left {
  left: -40px;
}

.arrow.right {
  right: -40px;
}

.arrow:hover {
  color: #ffffff;
}

/* ✅ Fade-In & Slide-Up Animation */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ✅ Ensure Proper Fit in Mobile */
@media (max-width: 768px) {
  .project-list {
    width: 100%;
  }

  .project-card {
    width: calc(100% - 20px);
    padding: 15px;
    margin-right: auto;
  }

  .project-title {
    font-size: 16px;
  }

  .project-description {
    font-size: 13px;
  }

  .tech-item {
    font-size: 11px;
    padding: 4px 8px;
  }
}
